import React from 'react';
import { Container, SideLeft, SideRight, LegendContainer, Legend } from './styles';
import  { ResponsiveContainer, BarChart, Bar, Cell, Tooltip } from 'recharts';
import formartCurrency from '../../utils/formatCurrency';

interface IBarChartBoxProps {
    title: string;
    data: {
        name: string;
        amount: Number;
        percent: number;
        color: string;
    }[];
}

const BarChartBox: React.FC<IBarChartBoxProps> = ({ title, data }) =>{
    return(
        <Container>
            <SideLeft>
                <h2>{title}</h2>
                <LegendContainer>
                    {data.map(indicator => (
                        <Legend key={indicator.name} color={indicator.color}>
                        <div>{indicator.percent ? indicator.percent : 0}%</div>
                        <span>{indicator.name}</span>
                    </Legend>
                    ))}
                </LegendContainer>
            </SideLeft>
            <SideRight>
                <ResponsiveContainer>
                    <BarChart data={data} >
                        <Bar dataKey="amount" name="Valor">
                            {data.map(indicator => (
                                <Cell 
                                    key={indicator.name}
                                    fill={indicator.color}
                                />
                            ))}
                        </Bar>
                        <Tooltip cursor={{ fill: 'none'}} 
                        formatter={(value) => formartCurrency(Number(value))}/>
                    </BarChart>
                </ResponsiveContainer>
            </SideRight>
        </Container>
    );
};

export default BarChartBox;